@import "colors";


// //== Scaffolding
// //
// //## Settings for some of the most global styles.

// //** Background color for `<body>`.
$body-bg:               $white;
// //** Global text color on `<body>`.
$text-color:            $gray-dark;

// //** Global textual link color.
$link-color:            $brand-primary;
// //** Link hover color set via `darken()` function.
$link-hover-color:      darken($link-color, 15%);
// //** Link hover decoration.
$link-hover-decoration: underline;


// //== Typography
// //
// //## Font, line-height, and color for body text, headings, and more.

$font-family-sans-serif:  'Helvetica Neue', Helvetica, Arial, sans-serif;
$font-family-serif:       Georgia, 'Times New Roman', Times, serif;
// //** Default monospace fonts for `<code>`, `<kbd>`, and `<pre>`.
// $font-family-monospace:   Menlo, Monaco, Consolas, "Courier New", monospace;
$font-family-base:        $font-family-sans-serif;

$font-size-base:          16px;
$font-size-large:         $font-size-base + 2; // ~18px
$font-size-small:         $font-size-base - 2; // ~12px

$font-size-h1:            36px;
$font-size-h2:            32px;
$font-size-h3:            30px;
$font-size-h4:            28px;
$font-size-h5:            26px;
$font-size-h6:            24px;

$line-height-h1:          $font-size-h1 + 6;
$line-height-h2:          $font-size-h2 + 6;
$line-height-h3:          $font-size-h3 + 4;
$line-height-h4:          $font-size-h4 + 4;
$line-height-h5:          $font-size-h5 + 8;
$line-height-h6:          $font-size-h6 + 8;

$hero-font-size-h1:            100px;
$hero-font-size-h2:            50px;
$hero-font-size-h3:            50px;
$hero-font-size-h4:            40px;
$hero-font-size-h5:            32px;
$hero-font-size-h6:            26px;

$hero-line-height-h1:          $hero-font-size-h1 + 10;
$hero-line-height-h2:          $hero-font-size-h2 + 6;
$hero-line-height-h3:          $hero-font-size-h3 + 6;
$hero-line-height-h4:          $hero-font-size-h4 + 8;
$hero-line-height-h5:          $hero-font-size-h5 + 4;
$hero-line-height-h6:          $hero-font-size-h6 + 4;

// //** Unit-less `line-height` for use in components like buttons.
// $line-height-base:        1.428571429; // 20/14
// //** Computed "line-height" (`font-size` * `line-height`) for use with `margin`, `padding`, etc.
// $line-height-computed:    floor(($font-size-base * $line-height-base)); // ~20px

// //** By default, this inherits from the `<body>`.
// $headings-font-family:    inherit;
// $headings-font-weight:    500;
// $headings-line-height:    1.1;
// $headings-color:          inherit;


