// //== Navbar
// //
// //##

// // Basics of a navbar
$navbar-height:                    50px;
$navbar-margin-bottom:             $line-height-computed;
$navbar-border-radius:             $border-radius-base;
$navbar-padding-horizontal:        floor(($grid-gutter-width / 2));
$navbar-padding-vertical:          (($navbar-height - $line-height-computed) / 2);
$navbar-collapse-max-height:       340px;

$navbar-color:             $gray-dark;
$navbar-bg:                $gray-lighter;
$navbar-border:            darken($navbar-bg, 6.5%);

// // Navbar links
$navbar-link-color:                $gray-dark;
$navbar-link-hover-color:          $gray-darker;
$navbar-link-hover-bg:             transparent;
$navbar-link-active-color:         #555;
$navbar-link-active-bg:            darken($navbar-bg, 6.5%);
$navbar-link-disabled-color:       #ccc;
$navbar-link-disabled-bg:          transparent;

// Navbar brand label
$navbar-brand-color:               $navbar-link-color;
$navbar-brand-hover-color:         darken($navbar-brand-color, 10%);
$navbar-brand-hover-bg:            transparent;

// Navbar toggle
$navbar-toggle-hover-bg:           #ddd;
$navbar-toggle-icon-bar-bg:        #888;
$navbar-toggle-border-color:       #ddd;


