@mixin contentTag() {
	padding:6px 8px;
	display:block;
	position: absolute;
	left:0;
	top:0;
	background:#333;
	color:$white;
	font-size:12px;
	font-weight:200;
}
.edit {
	.overlay {
		display:none;
	}
	.success-container {
		display:block!important;
	}
	.bannerImage__container, .hero__slider .hero__image {
		div[data-content="content"] {
			min-height:250px;
			background:$gray-dark;
		}
	}
	.hero__caption {
		div[data-content="content"] {
			min-width:150px;
		}
	}
	
