// Global styles
// =================================================
img {
  max-width:100%;
}
.master-container {
  position:relative;
  overflow:hidden;
}
#main {
  outline: 0;
}
.input-wrapper,.relative {
  position:relative;
}
.absolute {
  position:absolute;
}
.hidden-content, .hidden-alert {
  display: none;
}
.inner-xs     { padding: 5px;  @include clearfix; }
.inner-sm     { padding: 10px; @include clearfix; }
.inner        { padding: 15px; @include clearfix; }
.inner-md     { padding: 20px; @include clearfix; }
.inner-lg     { padding: 30px; @include clearfix; }
.inner-xl     { padding: 50px; @include clearfix; }

.padder-xs    { padding: 5px; }
.padder-sm    { padding: 10px; }
.padder       { padding: 15px; }
.padder-md    { padding: 20px; }
.padder-lg    { padding: 30px; }
.padder-xl    { padding: 50px; }
.padder-n,
.padder-none    { padding: 0; }

.padder-v-xs  { padding-top: 5px; padding-bottom: 5px;}
.padder-v-sm  { padding-top: 10px; padding-bottom: 10px;}
.padder-v     { padding-top: 15px; padding-bottom: 15px;}
.padder-v-md  { padding-top: 20px; padding-bottom: 20px;}
.padder-v-lg  { padding-top: 30px; padding-bottom: 30px;}
.padder-v-xl  { padding-top: 50px; padding-bottom: 50px;}

.no-gutter {
  [class^="span-"] {
    padding-left: 0;
    padding-right: 0;
  }
}
.m-xs     { margin: 5px; }
.m-sm     { margin: 10px; }
.m        { margin: 15px; }
.m-md     { margin: 20px; }
.m-lg     { margin: 30px; }
.m-xl     { margin: 50px; }
.m-n,.m-none  { margin: 0; }

.m-t-none   { margin-top: 0; }
.m-t-xs     { margin-top: 5px; }
.m-t-sm     { margin-top: 10px; }
.m-t        { margin-top: 15px; }
.m-t-md     { margin-top: 20px; }
.m-t-lg     { margin-top: 30px; }
.m-t-xl     { margin-top: 50px; }

.m-b-none   { margin-bottom: 0; }
.m-b-xs     { margin-bottom: 5px; }
.m-b-sm     { margin-bottom: 10px; }
.m-b        { margin-bottom: 15px; }
.m-b-md     { margin-bottom: 20px; }
.m-b-lg     { margin-bottom: 30px; }
.m-b-xl     { margin-bottom: 50px; }

.no-radius    { border-radius: 0;}

.b        { border: 1px solid #ebebeb; }
.bordered     { border: 1px solid #d1d3d4; }

.img-circle   { border-radius: 50%; }

.text-center  { text-align: center; }
.text-left    { text-align: left; }
.text-right   { text-align: right; }
// Backgrounds
.bg-light     { background-color: $gray-lighter; }
.bg-white     { background-color: $white; }
.bg-gray      { background-color: $gray; }
.bg-default   { background-color: $brand-default; }
.bg-primary   { background-color: $brand-primary; }
.bg-black     { background-color: $gray-base; }


.break-mobile {
  @media (max-width:$screen-xs-max) {
    margin-top:10px;
    li {
        display:block;
        width:100%;
        margin-bottom:10px;
    }
  }
}


