// LOADING SCREEN FOR SLIDESHOWS AND IMAGE BLOCKS
// =================================================
.loader {
  position:relative;
  .loading {
    position:relative;
    top:50%;
    margin-top:-30;
    .loading-inner {
      margin:0 auto;
    }
  }
  .load-watch {
    opacity:0;
  }
  overflow:hidden;
  height:440px;
  @media (min-width:$screen-sm-min) {
    height:440px;
  }
  @media (min-width:$screen-md-min) {
    height:80vh;
  }
  &.grid-lines {
    background: repeating-linear-gradient(
      -55deg,
      rgba($white,0.1),
      rgba($white,0.1) 5,
      rgba($gray,0.1) 5,
      rgba($gray,0.1) 6
    );
    border-bottom:1px solid rgba($gray,0.1);
    border-top:1px solid rgba($gray,0.1);
  }
  &.smaller {
      height:240px;
    @media (min-width:$screen-sm-min) {
      height:240px;
    }
    @media (min-width:$screen-md-min) {
      height:240px;
    }
  }
}

