.flex-video {
  position: relative;
  padding-top: 25px;
  padding-bottom: 67.5%;
  height: 0;
  margin-bottom: 16px;
  overflow: hidden;
}

.flex-video.widescreen { padding-bottom: 57.25%; }
.flex-video.vimeo { padding-top: 0; }

.flex-video iframe,
.flex-video object,
.flex-video embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
@media only screen and (max-device-width: 800px), only screen and (device-width: 1024px) and (device-height: 600px), only screen and (width: 1280px) and (orientation: landscape), only screen and (device-width: 800px), only screen and (max-width: 767px) {
  .flex-video { padding-top: 0; }
}

//loading animation styles
//==================================================
span.loading {
   &,span.loading-inner {
    display:block;
   }
}

.loading {
  padding: 10px 0;
}
.loading-inner {
    border: 5px solid #f3f3f3;
    border-top: 5px solid #333333;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    @include animation(spin 1.5s linear infinite);
}
@-webkit-keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

#complianceMenu,#acrobatNotice {
  &.focused,&:focus {
    display:block;
    opacity:1;
    min-width:200px;
    height:auto;
    width:auto;
    clip:auto;
    overflow:visible;
    padding: 1.25rem;
    margin: 0;
    top:1rem;
    left:1rem;
    z-index:99999;
    background:$gray-dark;
    a {
      color:$white;
      text-decoration: underline;
      &:hover,&:focus {
        text-decoration: none;
      }
    }
  }
}

// ADA and screen reader styles
//==================================================

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
}
.hidden-compliance {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
  &:focus {
    background-color: $brand-default;
    color: #fff;
    top: 16px;
    left: 10px;
    width: auto;
    height: auto;
    padding: 20px;
    margin: 0;
    clip: auto;
    display: block;
    z-index: 1000000;
  }
}

// Use in conjunction with .sr-only to only display content when it's focused.
// Useful for "Skip to main content" links; see http://www.w3.org/TR/2013/NOTE-WCAG20-TECHS-20130905/G1
// Credit: HTML5 Boilerplate

.sr-only-focusable {
  &:active,
  &:focus {
    position: static;
    width: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    clip: auto;
  }
}